@media (max-width: 992px) {
  .header .top-header__column .icon-menu {
    display: block;
    position: relative;
    width: 30px;
    height: 18px;
    cursor: pointer;
    z-index: 5; }
    .header .top-header__column .icon-menu span {
      transition: all 0.3s ease 0s;
      top: calc(50% - 1px);
      left: 0px;
      position: absolute;
      width: 100%;
      height: 2px;
      background: #ffffff; }
      .header .top-header__column .icon-menu span:first-child {
        top: 0px; }
      .header .top-header__column .icon-menu span:last-child {
        top: auto;
        bottom: 0px; }
    .header .top-header__column .icon-menu._active span {
      transform: scale(0); }
      .header .top-header__column .icon-menu._active span:first-child {
        transform: rotate(-45deg);
        top: calc(50% - 1px); }
      .header .top-header__column .icon-menu._active span:last-child {
        transform: rotate(45deg);
        top: calc(50% - 1px); }
  .header .top-header__column .menu__body {
    opacity: 0;
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    visibility: hidden;
