  .header .bottom-header .actions-header__item {
    padding: 0;
    background: none; }
  .header .bottom-header .info-header__schedule {
    display: none; }
  .page__side {
    margin: 0px 0px 30px 0px; }
  .page .menu-page__link {
    font-size: 14px;
    padding: 10px 20px; }
    .page .menu-page__link::after {
      transform: translate(0px, -50%) rotate(90deg); }
  .page .menu-page__parent._active .menu-page__link::after {
    transform: translate(0px, -50%) rotate(-90deg); }
  .page .submenu-page {
    background-color: #ffffff; }
    .page .submenu-page__product {
